.main-menu {
  position: relative;
  min-height: 56px;

  &__button {
    position: absolute;
    z-index: 6;
    right: 0;
    top: 0;
    font-size: 0;
    width: 23px;
    height: 23px;
    border: none;
    background: none;
    margin: 15px 20px;
    padding: 0;
    cursor: pointer;

    &:focus {
      outline: none;
    }

    @media @tablet-media {
      display: none;
    }
  }

  &__list {
    top: 0;
    z-index: 5;
    margin: 0;
    padding: 0;
    list-style: none;
    position: absolute;
    width: 100%;
    text-align: center;
    box-shadow: 0 -7px 7px -7px #6c96b0 inset;

    display: none;

    &.main-menu__list--opened {
      display: block;
    }

    @media @tablet-media {
      display: block;
      position: relative;
      background: transparent;
      box-shadow: none;
    }
  }

  &__item {
    &:not(:last-child) {
      border-bottom: 1px solid #678fa8;
      box-shadow: 0 1px 0 #9ac2db;

      @media @tablet-media {
        border: none;
        box-shadow: none;

        &:nth-child(2) .main-menu__link {
          margin-right: 20%;
        }
      }
    }
  }

  &__link {
    color: #ffffff;
    font-weight: bold;
    text-decoration: none;
    line-height: 55px;
    font-size: 18px;
    display: block;
    background-color: @color-primary;

    .styleguide();

    @media @tablet-media {
      background-color: #ffffff;
      font-size: 14px;
      width: 20%;
      float: left;
      line-height: 56px;

      color: #766357;

      &:hover {
        background-color: #ffffff;
        color: #604e43;
      }

      &:focus,
      &:active {
        color: #604e43;
      }

      &[href] {
        color: #000000;

        &:hover {
          background-color: #ffffff;
          color: @color-primary;
        }

        &:focus,
        &:active {
          color: rgba(0, 0, 0, 0.3);
          background-color: #ffffff;
        }
      }
    }
  }

  &__logo {
    position: absolute;
    z-index: 4;
    top: 0;
    left: 50%;
    transform: translateX(-50%);
  }
}

.main-menu__button::before {
  position: absolute;
  top: 9.5px;
  left: 0;
  content: "";
  width: 23px;
  height: 4px;
  background-color: @color-primary;
  box-shadow: 0 -9px 0 @color-primary, 0 9px 0 @color-primary;
  transition: all 0.3s ease-in-out 0s;

  .main-menu__button--opened& {
    box-shadow: 0 0 0 @color-primary, 0 0 0 @color-primary;
    transform: rotate(45deg);
    width: 27px;
    background-color: #ffffff;
  }
}

.main-menu__button::after {
  position: absolute;
  top: 9.5px;
  left: 0;
  content: "";
  width: 23px;
  height: 4px;
  background-color: @color-primary;
  transition: all 0.3s ease-in-out 0s;

  .main-menu__button--opened& {
    transform: rotate(135deg);
    width: 27px;
    background-color: #ffffff;
  }
}

.index-promo {
  min-height: 390px;
  position: relative;
  z-index: 2;
  text-align: left;
  background: url("../img/bg-back-mobile.jpg") no-repeat center top transparent;

  &::before {
    content: "";
    position: absolute;
    z-index: 3;
    width: 100%;
    height: 27px;
    bottom: 0;
    background: url("../img/bg-intro-triangle-mobile.svg") bottom center
      no-repeat transparent;

    @media @tablet-media {
      background: url("../img/bg-intro-triangle-tablet.svg") bottom center
        no-repeat transparent;
      height: 57px;
    }

    @media @desktop-media {
      background: url("../img/bg-intro-triangle-desktop.svg") bottom center
        no-repeat transparent;
    }
  }

  @media (min-resolution: 1.5dppx) {
    background: url("../img/bg-back-mobile@2x.jpg") no-repeat center top
      transparent;
    background-size: auto 100%;

    &::before {
      bottom: -1px;
    }
  }

  @media @tablet-media {
    min-height: 520px;
    margin-bottom: 10px;
    background: url("../img/bg-back-desktop.jpg") no-repeat center top
      transparent;

    @media (min-resolution: 1.5dppx) {
      background: url("../img/bg-back-desktop@2x.jpg") no-repeat center top
        transparent;
      background-size: auto 100%;
    }
  }

  &__pick {
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    padding: 0 20px;
    box-sizing: border-box;
    left: 0;
    right: 0;
    margin: 0 auto;

    @media @tablet-media {
      top: 252px;
    }
  }
}

.promo {
  min-height: 160px;
  position: relative;
  z-index: 2;
  text-align: left;
  background: url("../img/bg-back-mobile.jpg") no-repeat center bottom
    transparent;

  &::before {
    content: "";
    position: absolute;
    z-index: 3;
    height: 26px;
    width: 100%;
    bottom: -1px;
    background: url("../img/bg-intro-triangle-mobile.svg") bottom center
      no-repeat transparent;

    @media @tablet-media {
      background: url("../img/bg-intro-triangle-tablet.svg") bottom center
        no-repeat transparent;
      height: 57px;
    }

    @media @desktop-media {
      background: url("../img/bg-intro-triangle-desktop.svg") bottom center
        no-repeat transparent;
    }
  }

  @media @tablet-media {
    margin-bottom: 38px;
    min-height: 290px;
    background: url("../img/bg-back-desktop.jpg") no-repeat center bottom
      transparent;
  }

  @media @desktop-media {
    min-height: 300px;
    margin-bottom: 73px;
  }

  &__pick {
    position: absolute;
    top: 50px;
    padding: 0 45px;
    box-sizing: border-box;

    @media @tablet-media {
      margin: 0 auto;
      left: 0;
      top: 95px;
      right: 0;
    }
  }
}
